            const newRecipes = [...recipes];
            const selectedNewRecipe = newRecipes[recipeGroupIndex].recipes[recipeIndex];
            selectedNewRecipe.instructions = (wasInstructionsEdited) ? editedInstructions : selectedNewRecipe.instructions;
