import React, { useState, useEffect } from 'react';
import ReactDOM from 'react-dom';
import EditableTextField from './EditableTextField';
import validate from './validate';

const itemsAmount = ['single item', 'multiple items'];

const IngredientDialog = ({ 
    isOpen, 
    dialogType, 
    recipe, 
    index, 
    category, 
    onClose, 
    onSubmitIngredient, 
    onSubmitIngredients,
    unitsOfMeasure
}) => {

    const [ingredient, setIngredient] = useState('');
    const [ingredients, setIngredients] = useState('');
    const [recipeIngredients, setRecipeIngredients] = useState([]);
    const [unit, setUnit] = useState('');
    const [quantity, setQuantity] = useState('');
    const [items, setItems] = useState(itemsAmount[0]);

    const handleIngredientChange = (e) => setIngredient(e.target.value);
    const handleIngredientsChange = (e) => setIngredients(e.target.value);
    const handleUnitChange = (e) => setUnit(e.target.value);
    const handleQuantityChange = (e) => setQuantity(e.target.value);

    const modifyArray = (arr) => {
        return arr.map(item => {
            console.log(`IngredientDialog => item: ${item}`)
            if ((Number(item[0]) > 1) && (item[1][item[1].length - 1] !== 's')) item[1] += 's';
            if (item[item.length - 1] === true || item[item.length - 1] === false) item.pop();
            return item.join(' ');
        });
    };

    useEffect(() => {
        if (items === 'multiple items') {
            const ingredientDisplay = modifyArray(recipe.ingredients).join('\n');
            console.log(`IngredientDialog => ingredientDisplay: ${ingredientDisplay}`)
            setIngredients(ingredientDisplay);
        }
    }, [items]);
    useEffect(() => {
        if (isOpen && dialogType === 'edit') {
            setIngredient(recipe.ingredients[index][2]);
            setUnit(recipe.ingredients[index][1]);
            setQuantity(recipe.ingredients[index][0]);
        }
    }, [isOpen]);
    const processArray = (arr) => {
        if (arr.length > 3) {
            const joinedItems = arr.slice(2).join(' '); 
            return [...arr.slice(0, 2), joinedItems];
        }
        return arr;
    };
    useEffect(() => {
        console.log(JSON.stringify(ingredients, null, 2))
        const recipeIngredients = [];
        const ingredientsArray = ingredients.split('\n');
        const ingredientsArrays = ingredientsArray.map((ingredient) => {
            //alert(ingredient);
            const ingredientArray = processArray(ingredient.split(' '));
            console.log(`IngredientDialog =>  ingredientArray: ${ingredientArray}`)
            if (ingredientArray.length < 3 && Number(ingredientArray[0]) > 0) {
                //const newIngredientArray = ingredientArray.slice(1);
                ingredientArray[2] = ingredientArray[1];
                ingredientArray[1] = ''
            } else if (ingredientArray.length < 3) {
                ingredientArray[2] = `${ingredientArray[0]} ${ingredientArray[1]}`;
                ingredientArray[0] = '';
                ingredientArray[1] = '';
            }
            if (validate(ingredientArray[1]) !== null && ingredientArray[1].endsWith('ss')) {
                const ingredient = ingredientArray[1];
                ingredientArray[1] = ingredient.slice(0, -1);
            }
            recipeIngredients.push(ingredientArray);
        });
        console.log(`Recipe: ${JSON.stringify(recipeIngredients, null, 2)}`)
        //setIngredient(recipe.ingredients[index][2]);
        //setUnit(recipe.ingredients[index][1]);
        //setQuantity(recipe.ingredients[index][0]);
        setRecipeIngredients(recipeIngredients);
    }, [ingredients]);

    const handleItemsChange = (e) => {
        setItems(e.target.value)
    };

    const handleSubmit = () => {
        if (items === 'single item') {
            const ingredientData = {
                ingredient,
                unit,
                quantity: parseFloat(quantity)
            };
            onSubmitIngredient(ingredientData);
            onClose();
        } else {
            onSubmitIngredients(recipeIngredients);
        }
    };

    const handleCancel = () => {
        onClose();
    };

    if (!isOpen) return null;

    const toggleEditRecipe = () => alert(`IngredientDialog => toggleEditRecipe`);
    const editRecipe = () => alert(`IngredientDialog => editRecipe`);
    const editedRecipe = () => alert(`IngredientDialog => editedRecipe`);

    return <div className='modal-overlay bg-tintedDark'>
        <div className='containerBox modal p-20 color-lite bg-lite'>
            <form className='containerBox'>
                <div className='p-20 color-yellow bold contentCenter'>
                    {dialogType} {category}
                </div>
                <div className='containerBox form-group flexContainer'>
                    <label
                        className='flex2Column p-20 width-50-percent'
                        htmlFor='amount'
                    >
                        Items
                    </label>
                    <select
                        className='containerBox 
                                flex2Column'
                        id='items'
                        value={items}
                        onChange={handleItemsChange}
                    >
                        {itemsAmount.map((items) => (
                            <option
                                key={items}
                                value={items}
                            >
                                {items}
                            </option>
                        ))}
                    </select>
                </div>
                {
                    (items === 'single item')
                        ? <div>
                            <div className='containerBox form-group flexContainer'>
                                <label
                                    className='flex2Column p-20'
                                    htmlFor='ingredient'
                                >
                                    Ingredient
                                </label>
                                <input
                                    className='containerBox flex2Column width-50-percent'
                                    type='text'
                                    id='ingredient'
                                    value={ingredient}
                                    onChange={handleIngredientChange}
                                    required
                                />
                            </div>
                            <div className='containerBox form-group flexContainer'>
                                <label
                                    className='flex2Column p-20 width-50-percent'
                                    htmlFor='unit'
                                >
                                    Units
                                </label>
                                <select
                                    className='containerBox 
                                            flex2Column'
                                    id='unit'
                                    value={unit}
                                    onChange={handleUnitChange}
                                >
                                    {unitsOfMeasure.map((unit) => (
                                        <option
                                            key={unit}
                                            value={unit}
                                        >
                                            {unit}
                                        </option>
                                    ))}
                                </select>
                            </div>
                            <div className='containerBox form-group flexContainer'>
                                <label
                                    className='flex2Column p-20'
                                    htmlFor='quantity'
                                >
                                    Quantity
                                </label>
                                <input
                                    className='containerBox flex2Column width-50-percent'
                                    type='number'
                                    id='quantity'
                                    value={quantity}
                                    onChange={handleQuantityChange}
                                    required
                                    inputMode='numeric'
                                    pattern='[0-9]*'
                                />
                            </div>
                        </div>
                        : <div>
                            <EditableTextField
                                title='Ingredients:'
                                data={recipe.ingredients}
                                toggle={handleSubmit}
                                edit={editRecipe}
                                setEdited={setIngredients}
                                edited={ingredients}
                            />
                        </div>
                }

            </form>
            <div className='containerBox form-actions p-20 contentCenter'>
                <button
                    className='containerBox p-20 contentCenter button'
                    type='button'
                    onClick={handleSubmit}
                >
                    Submit
                </button>
                <button
                    className='containerBox p-20 contentCenter button'
                    type='button'
                    onClick={handleCancel}
                >
                    Cancel
                </button>
            </div>
        </div>
    </div>
};

export default IngredientDialog;